import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-email/@core/permissions/permission.store';
import { DataStoreService } from './pics-email/@core/service/data-store.service';
import { RBACINFO } from './pics-email/@core/urls/email-template-url.config';

@Component({
  selector: 'email',
  template: `
    <pics-email></pics-email>
  `,
  styles: [
  ]
})
export class EmailComponent implements OnInit {


  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public emailEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.emailEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
    })
  }

}
